<template>
    <div class="modal-layout">
        <div class="darken" @click="closeModal">
            <div class="new-modal gift-modal" :class="view" @click.stop="(showDropdown=false)">
                <!-- @click.stop="(showDropdown=false)" -->
                <div class="new-modal-back-button-container">
                    <button class="new-modal-back-button thin" @click="closeModal"></button>
                </div>
                <div class="modal-content">
                    <div class="window-title">{{$localizeFilter('send_gift')}}</div>
                    <div class="window-text">{{$localizeFilter('send_gift_text')}}</div>

                    <div class="gift-options-block" @click.stop="">
                        <GiftOptions class="gift-options" v-if="showOptions" :options="options" @showDropdownMethod="showDropdownMethod" @selectOption='selectOption' @clearRecipientId="clearRecipientId" :showDropdown="showDropdown"/>
                    </div>

                    <div class="options-hint">{{$localizeFilter('available_for_sending', [referralGiftsBalance])}}</div>

                    <div class="button-block">
                        <button class="action-button" @click="sendGift" :disabled="recipientId === null">
                            {{$localizeFilter('send_gift')}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import GiftOptions from '../GiftOptions.vue';

export default defineComponent({
    props: {
        button: {
            type: Object,
            default: null
        },
        view: {
            type: String,
            default: "center" // 'center', 'full'
        },
        referralGiftsBalance: {
            type: Number,
            default: 0
        }
    },
    setup() {
    },
    data: () => ({
        show: 1,
        showOptions: true,
        options: [],
        recipientId: null,
        showDropdown: false
    }),
    methods: {
        showDropdownMethod() {
            this.showDropdown = true
        },
        clearRecipientId()  {
            this.recipientId = null
        },
        closeModal() {
            this.$emit("closeModal");
        },
        selectOption(id) {
            this.recipientId = id
            this.showDropdown = false
            // console.log('selectOption')
        },
        async sendGift() {
            const data = {recipientId: this.recipientId}
            try {
                await this.$store.dispatch('sendReferralGift', data)
                this.$message(localizeFilter('gift_successfully_sent'))
                this.closeModal()
            }
            catch (e) {
                this.$error(e)
            }
        }
    },
    async mounted() {
        try {
            this.options = (await this.$store.dispatch('getReferralsForGift')).map(({giftAlreadySent, depositedInPools, ...res}) => {
                // console.log(giftAlreadySent, depositedInPools)
                if(giftAlreadySent || !+depositedInPools) {
                    return {...res, notAvailableMessage: giftAlreadySent ? 'gift_not_be_sent_reason_gift_already_sent' : 'gift_not_be_sent_reason_not_deposited_in_pools'}
                }
                return res;
            })

            // console.log(this.options)
        }
        catch (e) {
            this.$error(e)
        }
    },
    components: { GiftOptions }
})
</script>

