<template>
    <div class="table-options">
        <div class="window light input-block" @click="triggerDropdown">
            <!-- <label style="color: #EAEAEA;">{{}}</label> -->
            <input id="recipientName" class="recipient-name-input" :placeholder="$localizeFilter('enter_id_email_telegram_name')" type="text" v-model.trim="text"/>
            <!-- <button class="trigger-button">
                <img style="width:10px; height: 6px;" :src="require('@/assets/newImages/white_arrow.png')">
            </button> -->
        </div>
        <div class="window light dropdown-block" v-if="showDropdown && (visibleOptions.length > 0)">
        <ul>
            <li v-for="(option,idx) in visibleOptions" :key="idx" :class="'option' + (selectedIndex==idx ? 'selected' : '') + ' ' + (option.notAvailableMessage ? 'not-available' : '') " @click="selectOption(idx)">
                <div class="first-row">
                    {{option.name}}
                </div>
                <div class="second-row">
                    <div class="first-cell">{{option.id}}</div>
                    <div class="second-cell" v-if="option.notAvailableMessage">{{$localizeFilter(option.notAvailableMessage)}}</div>
                    <div class="second-cell" v-else>{{$utils.formatNumber((option.giftAmount / 1000000), 'en-US', 0, 2)}} USDT</div>
                </div>
            </li>
        </ul>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: [
        'options', 'showDropdown'
    ],
    data: ()=>({
        // isOpen: false ,
        selectedIndex: 0,
        selectedOption: null,
        text: null,
        search: null
    }),
    computed: {
        visibleOptions() {
            if (this.search == null) {
                return []
            } else {
                const result = []
                for (let i=0; i<this.options.length; i++) {
                    let el = this.options[i]
                    if (
                        el.name && el.name.toLowerCase().includes(this.search.toLowerCase()) || 
                        el.id && (el.id+'').toLowerCase().includes(this.search.toLowerCase()) || 
                        el.relegramName && el.telegramName.toLowerCase().includes(this.search.toLowerCase()) || 
                        el.email && el.email.toLowerCase().includes(this.search.toLowerCase())
                    ) { result.push(el) }
                }
                return result.slice(0, 3)
            }
        },

    },
    watch: {
        text(newVal, oldVal) {
            this.$nextTick(()=>{
                if (newVal === '') {
                    this.search = null
                    this.$emit('clearRecipientId')
                } else if (oldVal !== newVal){
                    this.search = newVal
                    for (let i=0; i<this.options.length; i++) {
                        let el = this.options[i]
                        if (el.name === newVal && el.id === this.selectedOption?.id) { return }
                    }
                    this.$emit('showDropdownMethod')
                    this.$emit('clearRecipientId')
                } 
            })
        }
    },
    methods: {
        triggerDropdown() {
            this.isOpen = !this.isOpen
        },
        selectOption(idx) {
            if(this.visibleOptions[idx].notAvailableMessage) { return }
            this.selectedIndex = idx
            this.$emit('selectOption', this.visibleOptions[idx]?.id)
            this.selectedOption = this.visibleOptions[idx]
            this.text = this.visibleOptions[idx].name
            console.log(this.selectedIndex)
            // this.isOpen = false
        }
    },
    mounted() {
    }
})
</script>

